.Expense {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  min-height: 4rem;
  background-color: #dadfe2;
  border-radius: 0px 13px 13px 0px;
  /* box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2); */
  /* padding: 20px; */
  /* outline: black; */
  transition-duration: 300ms;
}

.Expense-wrapper {
  display: flex;
  justify-content: space-between;
  border-radius: 0px 16px 16px 0px;
  box-shadow: 8px 8px 20px rgba(100, 100, 100, 0.5), 4px 4px 4px rgba(100, 100, 100, 0.25), -8px -8px 20px rgba(255, 255, 255, 0.75), -4px -4px 4px rgba(255, 255, 255, 0.5);;
  background: rgb(255,185,185);
  background: linear-gradient(90deg, rgba(255,185,185,1) 0%, rgba(255,234,178,1) 100%);
  width: 100%;
  /* height: 100%; */
  min-height: 4rem;
  padding: 3px;
  padding-left: 0px;
  margin-top: 2px;
  margin-bottom: 2px;
  transition-duration: 300ms;
}

.expense-text-block {
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.expenseName {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 0.75rem;
  color: grey;
  margin-left: 1rem;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
  width: auto;
}

.expensePerYr {
  font-weight: 700;
  font-size: 0.75rem;
  color: grey;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 1rem;
  padding-bottom: 0px;
  padding-top: 0px;
}

.expense-input {
  text-align: left;
  margin-top: 0px;

}

.expense-input-wrapper {
  display: flex;
  flex-direction: row;  
  margin-left: 1rem;
}

.dollar-sign {
  text-align: right;
  font-size: 1rem;
  font-weight: 700;
  padding: 0px;
  margin: 0px;
}

.delete-expense {
  background-color: transparent;
  border: none;
  font-size: 1rem;
  font-weight: 700;
  margin-right: 1rem;
  color: grey;
}

.delete-expense:hover {
  color: red;
  cursor: pointer;
}