.NetIncome {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  min-height: 5rem;
  background-color: #dadfe2;
  /* border-radius: 0px 13px 13px 0px; */
  /* box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2); */
  /* padding: 20px; */
  /* outline: black; */
  transition-duration: 300ms;
}

.NetIncome-wrapper {
  display: flex;
  justify-content: space-between;
  /* border-radius: 0px 16px 16px 0px; */
  box-shadow: 8px 8px 20px rgba(100, 100, 100, 0.5), 4px 4px 4px rgba(100, 100, 100, 0.25), -8px -8px 20px rgba(255, 255, 255, 0.75), -4px -4px 4px rgba(255, 255, 255, 0.5);;
  background: rgb(178,250,255);
  background: linear-gradient(90deg, rgba(178,250,255,1) 0%, rgba(186,255,185,1) 100%);
  width: 100%;
  height: 100%;
  min-height: 5rem;
  padding: 3px;
  padding-left: 0px;
  margin-top: 2px;
  margin-bottom: 2px;
  transition-duration: 300ms;
}

.net-income-amount {
  text-align: left;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 1.5rem;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 0rem;
  width: auto;
}

.net-dollar-sign {
  text-align: right;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 0px;
  margin: 0px;
}

.remaining-expenditures-containerd {
  display: flex;
  flex-direction: column;
}

.remaining-expenditures {
  font-weight: 700;
  font-size: 0.75rem;
  margin-left: 1rem;
  margin-bottom: 0px;
}

.remaining-expenditures-text {
  font-weight: 700;
  font-size: 0.75rem;
  margin-left: 1rem;
  margin-top: 0px;
  color: grey;
}