.Income {
  display: flex;
  flex-direction: column;
  align-items: right;
  align-content: right;
  text-align: right;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #dadfe2;
  border-radius: 13px 0px 0px 13px;
  /* padding: 20px; */
  /* margin: 5px; */
  padding: 0px;
  
}

.Income-wrapper {
  border-radius: 16px 0px 0px 16px;
  box-shadow: 8px 8px 20px rgba(100, 100, 100, 0.5), 4px 4px 4px rgba(100, 100, 100, 0.25), -8px -8px 20px rgba(255, 255, 255, 0.75), -4px -4px 4px rgba(255, 255, 255, 0.5);;
  background: rgb(178,250,255);
  background: linear-gradient(90deg, rgb(143, 244, 252) 0%, rgb(123, 253, 121)100%);
  width: 15%;
  height: 90%;
  padding: 3px;
  z-index: 2;
}

.income-input {
  align-items: right;
  text-align: right;
  margin: 0px;  
  font-size: 2.5rem;
  /* width: 7rem; */
}

.income-input-wrapper {

  display: flex;
  flex-direction: row;
  margin-right: 1rem;
  margin-left: auto;
}

.income-dollar-sign {
  font-size: 2.5rem;
  font-weight: 700;
  padding: 0px;
  margin: 0px;
}

.gross-income {
  color: grey;
  margin: 20px;
  margin-bottom: 0px;
  margin-top: 0px;
}