.App {
  width: 96vw;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  background-color: #dadfe2;
  /* background-color: grey; */
  padding-left: 2vw;
  padding-right: 2vw;
}

.Box-income {
  background-color: grey;
  width: 50%;
  height: 85%;
}

.outputs-container {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: left;
  /* width: 50%; */
  height: 90%;
  background-color: transparent;
  /* padding: 20px; */
}

.outputs-container-vertical {
  display: flex;
  flex-direction: column;
  background-color: transparent;
  height: 90%;
  align-items: center;
  text-align: center;
}


input {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 1rem;
  height: auto;
  width: 25%;
  background-color: transparent;
  border: none;
  color: rgb(55, 55, 55);
}

select {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 1rem;
  width: auto;
  background-color: transparent;
  border: none;
  margin-left: 1rem;
  color: rgb(55, 55, 55);
}

h3 {
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
}

p {
  font-family: 'Open Sans', sans-serif;
  color: rgb(55, 55, 55);
}